.App {
  text-align: center;
  font-family: Arial, sans-serif;
}

input {
  margin: 5px;
  padding: 10px;
  width: 250px;
}

button {
  margin: 5px;
  padding: 10px;
  cursor: pointer;
}

.chat-container {
  margin-top: 20px;
}

.messages {
  max-width: 400px;
  margin: 0 auto;
  text-align: left;
}

.message {
  background-color: #f1f1f1;
  padding: 10px;
  margin: 5px 0;
  border-radius: 5px;
}
